img,
.no-drag {
  -webkit-user-drag: none;
  -ms-user-select: none;
  -moz-user-select: none;
  -webkit-user-select: none;
  user-select: none;
}

a {
  text-decoration: none;
}

.vh-center {
  align-items: center;
  align-content: center;
  justify-content: center;
}

.btn {
  border: none;
  color: #fff;
  background-color: #b447d5;
  padding: 10px;
  border-radius: 05px;
  box-shadow: 0 8px 15px rgb(180 71 213 / 60%);
  outline: none;
  transition: .6s;
  cursor: pointer;
}

.btn:hover {
  background: var(--theme-color);
}

.btn:focus {
  box-shadow: 0 0 0 3px rgba(180, 71, 213, 0.5);
}

.danger-btn {
  background-color: rgb(255, 82, 82);
}

.danger-btn:hover {
  background-color: #e42a2a;
  box-shadow: 0 0 0 2px rgba(255, 82, 82, 0.479);
}

.danger-btn:focus {
  box-shadow: 0 0 0 3px rgba(255, 82, 82, 0.5);
}

.pointer {
  cursor: pointer;
}

.d-flex {
  display: flex;
}

hr {
  display: block;
  width: 100%;
  margin: 8px 0px;
  border: 0px solid transparent;
  border-bottom: 1px solid #c3c3c3;
  margin: 10px 0;
}

.top-nav {
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  box-shadow: 0px 2px 7px 0px rgb(0 0 0 / 23%);
  height: var(--top-nav-height);
  z-index: 1;
  align-items: center;
  justify-content: space-between;
  padding-right: 5px;
}

.top-nav .logo {
  max-height: var(--top-nav-height);
}

.left-nav {
  display: flex;
  position: fixed;
  top: var(--top-nav-height);
  left: 0;
  flex-direction: column;
  width: var(--left-nav-width);
  box-shadow: 2px 7px 11px 0px rgb(72 69 69 / 24%);
  bottom: 0;
  z-index: 999;
  border-top-right-radius: 5px;
}

.left-nav .brand-name {
  display: flex;
  background: var(--theme-color);
  height: 50px;
  font-size: 22px;
  padding-left: 10px;
  font-weight: 800;
  margin: 0;
  color: rgba(255, 255, 255, .8);
  align-items: center;
}

.left-nav .nav-features {
  padding: 0;
  display: flex;
  flex-flow: column;
}

.left-nav .nav-features a {
  padding: 10px 5px;
  font-weight: 500;
  font-size: 14px;
  border-bottom: 1px solid rgba(232, 232, 232, .7);
  transition: .6s;
  color: #7c7a7a;
}

.left-nav .nav-features a:hover {
  background: rgba(192, 192, 192, .2);
}

.left-nav .nav-features a.active {
  background: rgba(192, 192, 192, .2);
  color: var(--theme-color);
}

main {
  position: fixed;
  right: 0;
  bottom: 0;
  overflow-y: auto;
  top: var(--top-nav-height);
  left: var(--left-nav-width);
  background: linear-gradient(190deg, #fbe5f7, #fff);
  transition: 0.6s;
}


.card-hovered {
  font-family: sans-serif;
  padding: 10px;
  margin: 10px;
  background-color: rgba(255, 255, 255, 0.7);
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.161), 0 2px 10px 0 rgba(0, 0, 0, 0.122);
  border-radius: 3px;
}

.display-title {
  display: flex;
  align-items: center;
  background: rgba(var(--theme-color-coords), 0.25);
